	module.exports = {"data":{"markdownRemark":{"html":"<p>さて明日参加するさいたま・南浦和(さいたま文化センター)のオールジャンル同人即売会『<a href=\"http://www.h4.dion.ne.jp/%7Ewashoi/\">わっしょい</a>』での頒布作品情報ですが、<a href=\"/!/thA/\">時封城</a>のアレンジサントラ<a href=\"/!/leila/\">Leila Prismriver</a>(13曲65分:300円)及び東方のラミネートカード3種類(妖夢/魔理沙/秘封倶楽部:各1枚50円)を頒布いたします。やっぱり<em>暇</em>が予想されますので時封城の製作話云々とか新作話云々とか単に冷やかし云々とかに来ていただければありがたいです。  それと1994年に製作したゲーム『<a href=\"/2899\">赤い玉 青い玉 競走ゲーム</a>』をWindowsに移植したので公開します。</p>\n<p>さらに<a href=\"/emeditor-danmakufu\">EmEditor用東方弾幕風構文ファイル</a>を更新しました。今回はバグ修正のみの更新です。</p>","frontmatter":{"cover":null,"date":"2006-03-25T00:00:00+09:00","strDate":"2006/3/24","title":"明日即売会の作品情報"}}},"pathContext":{"path":"/ja/blog/2006/03/25-明日即売会の作品情報/","langKey":"ja"}}

